import {connect} from "react-redux";
import store from "../../store";
import {updateSiteModelChecklist} from "../../actions/cbChecklist_Actions";
import {updateCheckList} from "../../containers/cbChecklist/checkListMethods";


const updateChecklist = (property, value) => {
    let checklist = store.getState().siteModelChecklist?.currentChecklist ? store.getState().siteModelChecklist.currentChecklist : {};
    checklist[property]=value;
    store.dispatch(updateSiteModelChecklist({currentChecklist:checklist}));
}
const EditChecklistDetails = () => {
    let checklist = store.getState().siteModelChecklist?.currentChecklist ? store.getState().siteModelChecklist.currentChecklist : {};
    return <div>
        <form>
            <div>
                Name: <input type={"text"} value={checklist.name} onChange={(evt)=>updateChecklist("name", evt.currentTarget.value)} /><br/>
                Description: <input type={"text"} value={checklist.description} onChange={(evt)=>updateChecklist("description", evt.currentTarget.value)} /><br/>
                Active <input type={"checkbox"} checked={checklist.active} onChange={(evt)=>updateChecklist("active", evt.currentTarget.checked)} /><br/>
                <button type="button" onClick={()=>updateCheckList(Object.assign({},checklist))}>Save</button>
            </div>
        </form>
    </div>
}

const mapStateToProps = (state) =>{

    return {
        siteModelChecklist:state.siteModelChecklist,
    };
};

export default connect(mapStateToProps,{})(EditChecklistDetails);